.slide-container {
  position: relative;
  width: 300px;
  height: 380px;;
}

.slide-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slide-text {
  position: absolute;
  bottom: 20px;
  left: 20px;
  color: white;
  font-size: 24px;
  font-weight: bold;
  background-color: rgba(0, 0, 0, 0.5); /* Optional: to make the text stand out */
  padding: 10px;
  border-radius: 5px;
}

@media(max-width:580px){
  .slide-container{
    width:300px;
    height:320px;
  }
}

@media(max-width:380px){
  .slide-container{
    width:300px;
    height:320px;
  }
}