.hero{
    display:flex;
    flex-direction: row;
    margin-left:20px;
    margin-right:20px;
    margin-top:30px;
}

.images{
    max-width: 300px;
    max-height:380px;
}


@media(max-width:580px){
    .hero{
        display:flex;
        flex-direction: column;
        margin-left:20px;
        margin-right:20px;
        margin-top:30px;
        align-items: center;
        text-align: center;
        gap:10px;
    }
    
}

@media(max-width:520px){
    .hero{
        display:flex;
        flex-direction: column;
        margin-left:25px;
        margin-right:25px;
        margin-top:30px;
        align-items: center;
        text-align: center;
    }
    
}

